const OVERLAY = {
  position: 'fixed',
  top: 0,
  right: 0,
  bottom: 0,
  left: 0,
  width: '100%',
  height: '100%',
  overflow: 'auto',
  zIndex: 1000,
  padding: '20px',
  boxSizing: 'border-box',
  backgroundColor: 'rgba(0,0,0,0.75)',
  textAlign: 'center',
}
const MODALSTYLE = {
  position: 'fixed',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%,-50%)',
  backgroundColor: '#FFF',
  padding: '50px',
  zIndex: 1000
}
const CROSS = {
  position: 'absolute',
  top: '-12.5px',
  right: '-12.5px',
  display: 'block',
  width: '30px',
  height: '30px',
  textIndent: '-9999px',
  cursor: 'pointer',
  border: 'none',
  background: 'none',
  backgroundSize: 'contain',
  backgroundRepeat: 'no-repeat',
  backgroundPosition: 'center center',
  backgroundImage: 'url(data:image/png;base64,)',
}

export function Modal({ open, children, onClose, overlayClassName, modalClassName, crossClassName}){
  if(!open) return null
  return (
    <>
      <div style={OVERLAY} className={overlayClassName}  onClick={() => {onClose()}}>
        <div style={MODALSTYLE} className={modalClassName} onClick={e => {e.stopPropagation()}}>
          <button style={CROSS} onClick={onClose} className={crossClassName}>Close Modal</button>
          {children}
        </div>
      </div>
    </>
  )
}